.App {
  display: grid;
  grid-template-rows: auto 1fr;
}

.calendar_wrapper {
  transition: 0.3s;
}
.calendar_wrapper::-webkit-scrollbar {
  width: 0;
}
.open_and_date {
  display: flex;
  align-items: center;
  gap: 10px;
  margin: 15px 0;
}

.open_close {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10em;
  background-color: white;
  color: black;
}
