.time_wrapper {
  padding: 10px;
  border-radius: 10px;
  background-color: rgb(66, 66, 66);
}
.content_wrapper {
  border: 1px solid white;
  min-height: 40svh;
  padding: 10px;
  transition: 0.3s;
}
.content_wrapper textarea {
  width: 100%;
  background: transparent;
  color: white;
  font-size: 22px;
  resize: none;
  margin: 0 auto;
  display: block;
  border: none;
  transition: 0.3s;
  outline: none;
}
.content_wrapper button {
  cursor: pointer;
  padding: 10px 20px;
  border-radius: 10em;
  background-color: white;
  color: black;
}
.welcome {
  height: 100svh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.welcome_container{
  padding: 20px;
  border: 2px solid white;
  border-radius: 10px;
}
.input_button{
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.input_button input{
  outline: none;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid white;
  padding: 5px;
  width: 70%;
  color: white;
  font-size: 18px;
}
.input_button button{
  cursor: pointer;
  padding: 5px 15px;
  background-color: white;
  border-radius: 10em;
}